<template>
  <div class="outBox">
    <div class="my-box" :style="{backgroundImage: 'url(' + userInfo.userBg + ')'}">
      <div class="change-bg clearfix" v-if="ishas">
        <div class="fr cbg">
          <div >更换背景</div>
          <van-uploader class="upload-box" :after-read="afterRead" />
        </div>
      </div>
      <div class="placeLogin" v-else @click="goLogin">请登录</div>
      <div class="user-info">
        <div class="clearfix">
          <div class="my-img fl">
            <img class="auto-img" :src="userInfo.userImg" alt />
          </div>
          <div class="nickname fl">{{userInfo.nickName}}</div>
        </div>
        <div class="text">{{userInfo.desc == '' ? '这家伙很懒，什么也没留下' : userInfo.desc}}</div>
      </div>
    </div>

    <div>
      <van-cell-group>
        <van-cell :title="item.title" is-link v-for="(item, index) in litsData" :key="index" :to="{name:item.name}" />
      </van-cell-group>
    </div>
    <tabbar></tabbar>
  </div>
</template>

<script>
import tabbar from "../../components/tabbar";
import "../../assets/less/my.less";
export default {
  data() {
    return {
      ishas:true,//更换背景和请登录的显隐
      litsData: [
        { title: "账号管理",name:'accountManagement',},
        { title: "地址管理",name:'myAddress'},
        { title: "我的收藏",name:'myCollect'},
        { title: "安全中心",name:'securityCenter'}
      ],
      //用户信息
      userInfo: {},
      //base64
    //   serverBase64Img:null,
      
    };
  },
  components: {
    tabbar
  },
  created(){
      this.my()

      //
      let aa = Boolean(localStorage.getItem('isloginCbg'))
      
      this.ishas = aa
      
    
    //判断
    if(!localStorage.getItem('__Tk')){
      localStorage.setItem('isloginCbg','')
    }
  },
  mounted(){
    
    
  },
  methods: {
    //跳转登录
    goLogin(){
      this.$router.push({name:'login'})
    },
    //
    my() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'A001') {
            this.userInfo = result.data.result[0]
          }
        })
        .catch(() => {
          this.$toast.clear();
          
        });
    },
    afterRead(file) {

        let serverBase64Img = file.content.split(',')[1]
        // this.serverBase64Img = serverBase64Img
        //上传背景
      this.uploaderBgPhoto(serverBase64Img)
    
    },

    //上传用户背景图片
    uploaderBgPhoto(serverBase64Img){
        
        let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

        //规定图片类型
      let imgType = ['jpg','jpeg','png']

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          ...imgType,
          serverBase64Img
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'I001') {
              //更换背景图片
            this.userInfo.userBg = result.data.userBg;
          }
        })
        .catch(() => {
          this.$toast.clear();
          
        });
    
    }
   
  }
};
</script>

<style lang="less" scoped>
</style>